* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
a {
  color: inherit;
  text-decoration: none;
}
html {
  scroll-behavior: smooth;
  font-family: "SF Mono", Fallback, monospace;
}
img {
  cursor: pointer;
}
.hero {
  background: url("./images/back1.jpg") center/cover;
  height: 100vh;
  min-height: 50rem;
  position: relative;
  overflow: hidden;
  z-index: 5;
}
@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slide-in {
  0% {
    opacity: 0;
    top: -20px;
  }
  100% {
    opacity: 1;
    top: 0px;
  }
}
@keyframes slide-right {
  0% {
    opacity: 0;
    right: -100%;
  }
  100% {
    opacity: 1;
    right: 0;
  }
}
@keyframes slide-up {
  0% {
    opacity: 0;
    position: relative;
    top: 50px;
  }
  100% {
    position: static;
    opacity: 1;
    top: 0;
  }
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  list-style: none;
  counter-reset: item;
  font-weight: bolder;
  padding: 1rem 2rem;
  position: relative;
}

.nav li {
  position: relative;
  padding: 0 0.5rem;
  font-size: 1.2rem;
  color: #a8b2d1;
  transition: color 0.2s linear;
}
.large-nav li:nth-child(1) {
  animation: slide-in 0.2s ease-in forwards;
}
.large-nav li:nth-child(2) {
  animation: slide-in 0.4s ease-in forwards;
}
.large-nav li:nth-child(3) {
  animation: slide-in 0.6s ease-in forwards;
}
.large-nav li:nth-child(4) {
  animation: slide-in 0.8s ease-in forwards;
}
.large-nav li:nth-child(5) {
  animation: slide-in 1s ease-in forwards;
}
.nav li:hover {
  color: rgb(100, 255, 218);
}

.nav li a {
  padding: 7px;
}
.nav li::before {
  counter-increment: item;
  content: "0" counter(item) ".";
  text-align: right;
  color: rgb(100, 255, 218);
  font-size: 1rem;
}

.nav-icon {
  padding: 1rem;
  font-size: 2rem;
  position: absolute;
  right: 2rem;
  top: 2rem;
  z-index: 2;
  background-color: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;
  color: white;
}

.small-nav-container {
  display: none;
  position: relative;
}
.small-nav.hidden {
  display: none;
}
.hidden {
  display: none;
}

.intro {
  width: 60%;
  margin: auto;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: white;
  z-index: -1;
}
.colored {
  color: rgb(100, 255, 218);
  font-size: 1.5rem;
  text-transform: uppercase;
  position: relative;
  animation: slide-in 0.2s ease-in forwards;
}
.intro h1 {
  font-size: 4rem;
  position: relative;
  animation: slide-in 0.2s ease-in forwards;
}
.intro h3 {
  font-size: 2rem;
  text-transform: uppercase;
  color: #a8b2d1;
  margin: 1rem 0;
  position: relative;
  animation: slide-in 0.2s ease-in forwards;
}
.intro .my-info {
  margin: 1rem 0;
  font-size: 1.5rem;
  width: 70%;
  position: relative;
  animation: slide-in 0.2s ease-in forwards;
}
.intro a {
  border: 1px solid rgb(100, 255, 218);
  padding: 10px 15px;
  font-size: 1.2rem;
  background-color: transparent;
  color: rgb(100, 255, 218);
  display: inline-block;
  margin: 0.7rem 0;
  z-index: 2;
  position: relative;
  animation: slide-in 0.2s ease-in forwards;
}
.intro a:hover {
  background-color: rgba(100, 255, 218, 0.2);
}
.about-container {
  background-color: #27496d;
  width: 100%;
}
.about {
  width: 60%;
  margin: auto;
  padding: 10% 0;
  color: white;

  font-size: 1.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about p {
  margin: 1rem 0;
  width: 70%;
}
.about img {
  border-radius: 50%;
  border: 10px solid gray;
  cursor: pointer;
}
main {
  counter-reset: section;

  background-color: #296169;
  color: white;
  padding: 2rem 0;
}
main > h2 {
  text-align: center;
}
.section {
  width: 60%;
  margin: auto;
  padding: 2rem 0;
  animation: slide-up 0.5s linear;
}
.title {
  font-size: 1.4rem;
  margin: 1rem 0;
}
.title.right {
  width: 65%;
  margin-left: auto;
}
.title::before {
  counter-increment: section;
  content: "0" counter(section) ".";
}
.title::after {
  content: "";
  display: inline-block;
  width: 400px;
  height: 2px;
  background-color: #a8b2d1;
  margin: 0 1rem;
}
.section .content {
  display: flex;
  flex-flow: row wrap-reverse;
  justify-content: space-between;
  align-items: center;
}
.wrap-normal {
  flex-wrap: wrap !important;
}
.section .content p {
  width: 50%;
}
.web-image {
  height: 300px;
  width: 300px;
  border-radius: 50%;
  border: 5px solid gray;
  margin: auto;
}
.cert-container {
  background-color: #00a8cc;

  padding: 2rem 0;
  color: white;
}
.cert-container > h2 {
  text-align: center;
}
.cert {
  width: 70%;
  margin: 1rem auto;
  text-align: center;
}

.cert img {
  max-width: 50%;
  cursor: pointer;
}
.work-container {
  width: 100%;
  background-color: #1eb2a6;
  counter-reset: section;
  font-size: 1.2rem;
  color: white;
  padding: 2rem 0;
}
.work-container > h2 {
  text-align: center;
}
.work-container .section {
  width: 60%;
}
.work-container .title::after {
  width: 100%;
}
.work-container .section .content p {
  width: 90%;
  margin: 1rem;
}
.work-imgs-div {
  width: 100%;
  display: flex;
  position: relative;
  justify-content: space-around;
  cursor: pointer;
}
.work-imgs-div:hover .work-overlay {
  display: block;
}
.work-overlay {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  animation: show 0.3s ease;
}

.work-overlay a {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid rgb(100, 255, 218);
  padding: 10px 15px;
  font-size: 1.2rem;
  background-color: transparent;
  color: rgb(100, 255, 218);
  display: inline-block;
  margin: 0.7rem 0;
  z-index: 2;
}
.work-overlay a:hover {
  background-color: rgba(100, 255, 218, 0.2);
}
.work-image-s {
  width: 20%;
  height: 25rem;
  margin: 0.5rem;
}
.work-image-l {
  width: 75%;
  height: 25rem;
  margin: 0.5rem;
}

.contact-container {
  width: 100%;
  background-color: rgb(36, 47, 56);
  padding: 2rem 0;
  color: white;
}
.contact-container > h2 {
  text-align: center;
}
.contact {
  width: 60%;
  margin: auto;
}
.contact .social {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
  font-size: 1.5rem;
}
.contact .social a {
  margin: 0 1rem;
}

.contact form {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: 1rem auto;
  justify-content: center;
}
.contact input,
.contact textarea {
  padding: 0.8rem 0.5rem;
  background-color: rgb(14, 16, 17);
  margin-bottom: 0.5rem;
  border-radius: 5px;
  color: gray;
  border: none;
  font-family: "Times New Roman", Times, serif;
  font-size: 1.2rem;
}

.contact textarea {
  height: 10rem;
  resize: none;
}
.contact button {
  border: 1px solid rgb(100, 255, 218);
  padding: 10px 15px;
  font-size: 1.2rem;
  background-color: transparent;
  color: rgb(100, 255, 218);
  display: inline-block;
  margin: 0.7rem 0;
  z-index: 2;
  cursor: pointer;
}
.contact button:hover {
  background-color: rgba(100, 255, 218, 0.2);
}
footer {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  flex-wrap: wrap;
  background-color: rgb(14, 16, 17);
  color: gray;
}
@media (max-width: 1400px) {
  .section .content p {
    width: 100%;
    padding: 2rem 0;
  }
  .title.right {
    margin: 0;
  }
  .title::after {
    display: block;
    margin-top: 1rem;
  }
  .about {
    width: 80%;
  }
  .about p {
    width: 80%;
  }
  .about img {
    width: 70%;
  }
  .work-container .section {
    width: 80%;
    font-size: 1.2rem;
  }
  .work-container .section .content p {
    width: 100%;
    margin: 1rem;
  }
  .contact {
    width: 100%;
  }
  .contact form {
    width: 90%;
  }
}

@media (max-width: 992px) {
  footer {
    align-items: center;
    flex-direction: column;
  }
  .about {
    flex-wrap: wrap;
  }
}
@media (max-width: 756px) {
  .work-container .section {
    width: 95%;
  }
  .large-nav {
    display: none;
  }
  .small-nav-container {
    display: block;

    animation: slide-right 0.2s ease forwards;
  }
  .real-small-container {
    display: block;
    background-color: rgb(23, 42, 69);
    width: 100%;
    height: 100%;
    position: fixed;
    animation: slide-right 0.4s ease forwards;
    z-index: 5;
  }
  .title::after {
    margin-left: 0;
  }

  .small-nav {
    display: flex;
    flex-direction: column;
    padding-top: 8rem;
  }
  .small-nav li {
    padding: 1rem 0;
    text-align: center;
  }
  .intro {
    width: 90%;
  }
  .intro .my-info {
    width: 100%;
  }
  .section {
    width: 90%;
  }

  .about {
    width: 95%;
  }
  .about div {
    width: 90%;
    margin: auto;
  }
  .cert img {
    max-width: 100%;
  }
}
@media (max-width: 400px) {
  .title::after {
    width: 200px;
  }
}

.overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}
.overlay .times-img {
  color: white;
  font-size: 2rem;
  position: absolute;
  top: 1rem;
  right: 2rem;
  cursor: pointer;
}
.overlay img {
  width: 80%;
  height: 80%;
}

.loader {
  -webkit-animation: sk-scaleout 1s infinite ease-in-out;
  animation: sk-scaleout 1s infinite linear;
  border-top: 1px solid rgb(100, 255, 218);
  border-radius: 100%;
  height: 6em;
  width: 6em;
}

.loader-container {
  align-items: center;
  background-color: #27496d;
  display: flex;
  height: 100vh;
  justify-content: center;
}

@keyframes sk-scaleout {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);

    transform: rotate(360deg);
  }
}
