.admin-main {
  width: 100%;
  position: relative;
}
.form-container {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50%;
  transform: translate(-50%, -50%);
}
.form-container form {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 1rem auto;
  justify-content: center;
}
.form-container input {
  padding: 0.8rem 0.5rem;
  background-color: rgb(14, 16, 17);
  margin-bottom: 0.5rem;
  border-radius: 5px;
  color: gray;
  border: none;
  font-family: "Times New Roman", Times, serif;
  font-size: 1.2rem;
}

.admin-main button {
  border: 1px solid rgb(100, 255, 218);
  padding: 10px 15px;
  font-size: 1.2rem;
  background-color: transparent;
  color: rgb(100, 255, 218);
  display: inline-block;
  margin: 0.7rem 0;
  z-index: 2;
  cursor: pointer;
}
.admin-main button:hover {
  background-color: rgba(100, 255, 218, 0.2);
}

.admin-board {
  width: 60%;
  margin: 2rem auto;
}
.admin-board .admin-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.messages-box {
  margin: 1rem 0;
  overflow: hidden;
}
.messages-box ul {
  list-style-type: none;
}
.messages-box li {
  border: 1px solid black;
  border-radius: 10px;
  padding: 1rem;
  margin: 1rem;
}
.message-header {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
}
.messages-box .sender-info h1 {
  display: flex;
  justify-content: center;
  align-items: center;
}
.read-circle {
  width: 25px;
  height: 25px;
  background-color: red;
  display: inline-block;
  border-radius: 50%;
  margin-left: 1rem;
}
.messages-box h2 {
  margin: 1rem;
}
@media (max-width: 992px) {
  .form-container {
    width: 90%;
  }
  .admin-board {
    width: 95%;
  }
}
@media (max-width: 500px) {
  .admin-board .admin-header {
    flex-direction: column;
  }
}
